import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { TipoInspecao } from './../../../model/objetc/tipoinspecao';
import { TipoinspecaoService } from './../../../service/sigobra/tipoinspecao.service';
import { Base } from '../../../model/base';

@Component({
  selector: 'app-tipoinspecoes',
  templateUrl: './tipoinspecoes.page.html',
  styleUrls: ['./tipoinspecoes.page.scss'],
})
export class TipoinspecoesPage implements OnInit {

  tipoinspecao: TipoInspecao;
  tipoinspecoes: Array<TipoInspecao>;  
  value: string = '';
  constructor(private base: Base, private tis: TipoinspecaoService, private router: Router) { }

  ngOnInit() {    

  }

  ionViewWillEnter() {
    this.carregartipoinspecoes(" ");
  }

  excluir(tipoinspecao: TipoInspecao) {
    this.base.Confirma('Deseja excluir essa empresa', this.removerativo.bind(this), tipoinspecao);
  }

  remover(tipoinspecao: TipoInspecao) {
    this.base.present();   
    return this.tis.delete(tipoinspecao).subscribe(
      data => {
        this.base.dismiss();
        this.base.menssagemSucesso('Tipo inspecao Excluida');
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Falha  ao Excluir  empresa '+ tipoinspecao.id + this.base.tratarErro(error));  
      }
    );
  }



  editar(tipoinspecao: TipoInspecao) { 
    this.tis.tipoinspecao = tipoinspecao;
    this.router.navigate(['tipoinspecao-inserir']);   
  }  
  
  carregartipoinspecao(value: string) {
    this.base.present();
    return this.tis.pesquisar(value).subscribe(
      data => {
        this.tipoinspecoes = data as Array<TipoInspecao>;
        this.base.dismiss();
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Falha  ao listar  tipoinspecao ' + this.base.tratarErro(error));
      }
    );
  }

  carregartipoinspecoes(value: string) {
    this.base.present();
    return this.tis.pesquisa(value).subscribe(
      data => {
        this.tipoinspecoes = data as Array<TipoInspecao>;
        this.base.dismiss();
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Falha  ao listar  tipoinspecao ' + this.base.tratarErro(error));
      }
    );
  }

  inserir() {
    this.tis.tipoinspecao = new TipoInspecao;
    this.router.navigate(['tipoinspecao-inserir']);
  }

  removerativo(tipoinspecao: TipoInspecao) {
    this.tipoinspecao= tipoinspecao;
    this.base.present();  
    if ( (this.tipoinspecao.ativo !== null)&& (this.tipoinspecao.ativo !== undefined)){      
      this.tipoinspecao.ativo = false;
    }  
    return this.tis.editarativo(this.tipoinspecao).subscribe(
      data => {        
        this.base.dismiss();
        this.router.navigate(['tipoinspecoes']);
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Falha  ao Excluir  tipoinspecao'+ this.tipoinspecao.id + this.tipoinspecao.ativo 
        + this.base.tratarErro(error));  
      }
    );           
  }

}
